import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import Navbar from './Navbar';
import { performLogin, setUserToken, performLogout, performNameChange } from '../actions/userActions';
import PropTypes from 'prop-types';
import { LOGIN_URL, LOGOUT_URL, REGISTER_URL, USER_AUTH_URL } from '../listrUrls';
import CSRFToken from './csrftoken';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  layout: {
    width: 'auto',
    display: 'block', // Fix IE11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    marginRight: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  form: {
    width: '100%', // Fix IE11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
});

class Account extends Component {

  constructor(props){
    super(props);
    this.state = {
      newUsername: null,
      newUsername2: null,
      newPassword: null,
      newPassword2: null
    }
  }
  componentDidMount(){
    this.checkIfLoggedIn();
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value})
  }

  handleNameChange = (e) => {
    e.preventDefault();
    if(this.state.newUsername == this.state.newUsername2){
      this.props.performNameChange(USER_AUTH_URL, {"username": this.state.newUsername}, this.props.userToken);
    }
  }

  checkIfLoggedIn(){
    var cachedToken = localStorage.getItem("token");
    if(cachedToken !== null && cachedToken !== undefined){
      this.props.setUserToken(cachedToken);
      return true;
    }
    return false;
  }


  render(){

    const { classes } = this.props;
    return(
      <Fragment>
        <Navbar />
        <CssBaseline />
        <Grid item xs={12} style={{padding: 40, marginTop: 10}}>
          <Grid container spacing={8} alignItems="center" justify="center">
            <Grid item xs={12} style={{marginBottom: 20}}>
              <header className="App-header" style={{display: "inline-flex"}}>
                <h1 className="App-title">My Account</h1>
              </header>
            </Grid>
            <Grid item md={4}>
              <Paper className={classes.paper}>
                  <header className="App-header">
                    <h3 className="App-title">Update Username</h3>
                  </header>
                  <form onSubmit={(e) => this.handleNameChange(e)}>
                    <FormControl margin="normal" fullWidth>
                      <InputLabel htmlFor="newUsername">Username</InputLabel>
                      <Input id="newUsername" onChange={(e) => this.handleChange(e)}/>
                    </FormControl>
                    <FormControl margin="normal" fullWidth>
                      <InputLabel htmlFor="newUsername2">Confirm Username</InputLabel>
                      <Input id="newUsername2" onChange={(e) => this.handleChange(e)}/>
                    </FormControl>
                    <Button
                      type="submit"
                      variant="raised"
                      color="primary"
                      >
                      Update
                    </Button>
                  </form>
              </Paper>
            </Grid>
            <Grid item md={4}>
              <Paper className={classes.paper}>
                  <header className="App-header">
                    <h3 className="App-title">Change Password</h3>
                  </header>
                  <form onSubmit={(e) => this.onRegister(e)}>
                    <FormControl margin="normal" fullWidth>
                      <InputLabel htmlFor="newPassword">New Password</InputLabel>
                      <Input id="newPassword" name="newPassword" autoComplete='off' type="password" variant="outlined" onChange={(e) => this.handleChange(e)}/>
                    </FormControl>
                    <FormControl margin="normal" fullWidth>
                      <InputLabel htmlFor="newPassword2">Confirm New Password</InputLabel>
                      <Input id="newPassword2" autoComplete='off' type="password" onChange={(e) => this.handleChange(e)}/>
                    </FormControl>
                    <Button
                      type="submit"
                      variant="raised"
                      color="primary"
                      >
                      Change Password
                    </Button>
                  </form>
              </Paper>
            </Grid>
          </Grid>
        </Grid>
      </Fragment>
    );
  }

}

Account.proptypes = {

}

const mapStateToProps = state => ({
  userToken: state.users.token,
});

const ToExport = withStyles(styles)(Account);
export default connect(mapStateToProps, { performLogin, setUserToken, performLogout, performNameChange })(ToExport);



