import React, { Component } from 'react';
import Account from './Account';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import AccountCircle from '@material-ui/icons/AccountCircle';
import PropTypes from 'prop-types';
import { LOGOUT_URL } from '../listrUrls';
import { Redirect, Route } from "react-router-dom";
import { performLogin, setUserToken, performLogout } from '../actions/userActions';



class Navbar extends Component {

  constructor(props){
    super(props);
    this.onLogout = this.onLogout.bind(this);
    this.state = {
      accountMenu: false,
      anchorEl: null,
      toAccount: false,
      toHome: false,
    }
    this.handleClose = this.handleClose.bind(this);
  }

  returnHome = (e) => {
    this.setState({ toHome: true });
  }

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  handleToAccount = () => {
    this.handleClose();
    this.setState({ toAccount: true });
  };

  onLogout(){
    localStorage.removeItem('token');
    this.props.performLogout(LOGOUT_URL);
  }

  render(){

    const styles = {
      root: {
        flexGrow: 1,
      },
      grow: {
        flexGrow: 1,
      },
      menuButton: {
        marginLeft: -12,
        marginRight: 20,
      },
    };

    const open = Boolean(this.state.anchorEl);

    if(this.state.toAccount){
      this.setState({ toAccount: false });
      return <Redirect to="/account/" push />
    }else if(this.state.toHome){
      this.setState({ toHome: false });
      return <Redirect to="/" push />
    }

    return(
      <div className={styles.root}>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="title" color="inherit" style={{ flex: 1 }} onClick={this.returnHome}>
              CollabPads
            </Typography>
            <IconButton
              aria-owns={open ? 'menu-appbar' : undefined}
              aria-haspopup="true"
              onClick={this.handleMenu}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={this.state.anchorEl}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={open}
              onClose={this.handleClose}
            >
              <MenuItem onClick={this.handleToAccount}>My Account</MenuItem>
              <MenuItem onClick={this.onLogout}>Logout</MenuItem>
            </Menu>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}
Navbar.proptypes = {
  performLogout: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { performLogout })(Navbar);



